<template lang="html">
  <p>
    <span>{{moment}}</span>
  </p>
</template>

<script>
export default {
  data () {
    return {
      moment: ''
    }
  },
  props: {
    originalYear: {
      type: Number,
      default: 2018
    },
    originalMonth: {
      type: Number,
      default: 1
    },
    originalDate: {
      type: Number,
      default: 1
    },
    originalHour: {
      type: Number,
      default: 0
    },
    originalMinute: {
      type: Number,
      default: 0
    },
    originalSecond: {
      type: Number,
      default: 0
    }
  },
  mounted () {
    this.runTime ()
  },
  methods: {
    // 
    runTime () {
      let self = this
      let time = setInterval (function () {
        let seconds = 1000
        let minutes = seconds * 60
        let hours = minutes * 60
        let days = hours * 24
        let years = days * 365
        let today = new Date()
        let todayYear = today.getFullYear()
        let todayMonth = today.getMonth()
        let todayDate = today.getDate()
        let todayHour = today.getHours()
        let todayMinute = today.getMinutes()
        let todaySecond = today.getSeconds()
        /* Date.UTC() -- date(UTC)197011()
        year - date4
        month - 0-11date
        day - 1-31date
        hours - 0(24)-23date
        minutes - 0-59date
        seconds - 0-59date
        microseconds - 0-999date */
        let t1 = Date.UTC(self.originalYear,self.originalMonth,self.originalDate,self.originalHour,self.originalMinute,self.originalSecond)
        let t2 = Date.UTC(todayYear,todayMonth,todayDate,todayHour,todayMinute,todaySecond)
        let diff = t2-t1
        let diffYears = Math.floor(diff/years)
        let diffDays = Math.floor((diff/days)-diffYears*365)
        let diffHours = Math.floor((diff-(diffYears*365+diffDays)*days)/hours)
        let diffMinutes = Math.floor((diff-(diffYears*365+diffDays)*days-diffHours*hours)/minutes)
        let diffSeconds = Math.floor((diff-(diffYears*365+diffDays)*days-diffHours*hours-diffMinutes*minutes)/seconds)
        self.moment =diffYears+"  "+diffDays+"  "+diffHours+"  "+diffMinutes+"  "+diffSeconds+" "
      }, 1000)
    }
  }
}
</script>



